.guide {
    width: 100%;
    position: relative;

    &__wrap {
        padding-top: 80px;
        position: relative;
        display: flex;
    }

    &__menu {
        min-width: 200px;
        width: 200px;
        border-right: 1px solid #e3e3e3;
        position: fixed;
        height: calc(100vh - 80px);
        left: 0;
        top: 80px;
        background-color: #F8F9FC;
        z-index: 100;

        button {
            width: 100%;
            text-align: left;
            padding: 0 10px;
            font-weight: bold;
            border-bottom: 1px solid #e3e3e3;
            background-color: color(blue, 800);

            &.is-active {
                +ul {
                    display: block;
                }
            }

            +ul {
                display: none;

                >li {
                    >a {
                        line-height: 38px;
                        display: block;
                        height: 40px;
                        border-bottom: 1px solid #e3e3e3;
                        font-size: 14px;
                        padding: 0 16px;
                    }
                }
            }
        }
    }

    &__content {
        padding-left: 200px;
        width: 100%;
        height: 100%;
    }
}

.guide-wrap {
    position: relative;
    padding: 20px;
    background-color: #FBFBFD;
}

.ex-name {
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 15px;
}

.ex-content {
    margin-top: 15px;
    border: 1px solid #bbcdd9;
    border-radius: 5px;
    background-color: #fff;
    padding: 15px;
}

.ex-content.desc {
    margin-top: 15px;
    border: 1px solid #bbcdd9;
    border-radius: 5px;
    background-color: #354052;
    padding: 15px;
}

.ex-title {
    padding-bottom: 10px;
    border-bottom: 1px solid #bbcdd9;
}

.ex-title>h3 {
    font-size: 20px;
    font-weight: bold;
}

.ex-preview {
    padding: 10px 0 25px;
}

.ex-desc {
    font-size: 14px;
    color: #1d1d1d;
    line-height: 1.8;
    margin-top: 5px;
}

.ex-class {
    display: inline-block;
    padding: 0px 5px;
    background-color: #202634;
    opacity: 1;
    color: #66d9ef;
    line-height: 1.5;
    font-size: 14px;
    font-family: sans-serif;
    border-radius: 5px;
}

.ex-body {
    padding-top: 10px;
    position: relative;
}

.ex-body pre {
    background-color: #202634;
}

.html-view {
    display: block;
    position: relative;
    width: 100%;
}

.ex-code-view-btn .html-view-btn,
.ex-code-view-btn .pug-view-btn {
    border: 0;
    font-size: 12px;
    padding: 5px 10px;
    border-radius: 10px 10px 0 0;
    color: #fff;
    background-color: #bdbebd;
}

.html-view-btn.active,
.pug-view-btn.active {
    background-color: #272822;
}

.language-html,
.language-pug {
    margin: 0 !important;
    border-radius: 0 !important;
}

.common-class-desc {
    position: fixed;
    width: 100%;
    z-index: 10;
    top: 20px;
    font-size: 12.5px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #354052;
    color: #ecf2f6;
    padding: 10px 0px;
    margin-bottom: 10px;
    margin: -20px;
}

.class-desc {
    padding: 0 10px;
    background-color: #292f3e;
    border-radius: 5.5px;
    margin: 0 10px;
    padding: 2px 5px;
    margin: 0 10px;
}